.filterWrapper {
    position: absolute;
    z-index: 2;
    left: 0;
    right: 0;
}

.title {
    color: #000;

    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 130%; /* 20.8px */
    margin: 0 0 15px 0;
}

.custom_radio, .custom_checkboxes {
    display: flex;
    align-items: center;
    cursor: pointer;
    user-select: none;
    margin-bottom: 5px;
}

.custom_radio > input, .custom_checkboxes > input {
    position: absolute;
    z-index: -1;
    opacity: 0;
    width: 0 !important;
}

.custom_radio > .custom_radio_span::before, .custom_checkboxes > .custom_checkboxes_span::before {
    content: '';
    display: inline-block;
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 2px solid #DAD3D3;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 90% 80%;
    margin-top: 4px;
    border-radius: 100%;
    transition: 0.3s ease;
}

.custom_checkboxes:hover > .custom_checkboxes_span::before {
    border: 2px solid #8C8B8B;
}

.custom_checkboxes:hover > .text {
    color: #000000;
}

